:root {
  --background: #12141a;
  --component: #12141a;
  --text: #ffffff;
  --subtext: #ccc;
  --link: #3355ff;
  --theme: #3150ff;
  --slider: #bbbbbb;
  --shade-1: #111111;
  --shade-2: #222222;
  --shade-3: #333333;
  --theme-red: rgb(220, 30, 50);
  --theme-green: rgb(60, 200, 80);
  --theme-orange: rgb(255, 200, 30);
}

@media (prefers-color-scheme: light) {
   :root {
    --background: #ffffff;
    --component: #eeeeee;
    --text: #000000;
    --subtext: #222222;
    --link: #3355ff;
    --theme: #3150ff;
    --slider: #bbbbbb;
    --shade-1: #ffffff;
    --shade-2: #eeeeee;
    --shade-3: #dddddd;
    --theme-red: rgb(220, 30, 50);
    --theme-green: rgb(60, 200, 80);
    --theme-orange: rgb(255, 200, 30);
  }
}

* {
  box-sizing: border-box;
}

::selection {
  background-color: rgba(100, 150, 255, 0.3);
  color: var(--text);
}

body {
  background: var(--background);
  color: var(--text);
  padding: 0;
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  overflow-x: hidden;
}

.app-container {
  width: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 0 10vw 0 10vw;
}

@media screen and (max-width: 768px) {
  .app-container {
    padding: 1rem;
    margin-top: 2rem;
    width: 100% !important;
  }
  .logo {
    top: 0 !important;
    left: 0 !important;
    padding: 0.8rem;
    background: var(--component);
    width: 100%;
  }
  .statistics {
    display: none;
  }
}

.logo {
  top: 3rem;
  left: 5rem;
  position: fixed;
  display: flex;
  z-index: 1;
}
.logo img {
  width: 4rem; 
  height: 4rem;
  vertical-align: middle;
}
.logo h2 {
  font-size: 1.5rem;
  font-weight: 500;
  color: var(--text);
  margin-left: 2rem;
}
.statistics {
  display: flex;
  justify-content: space-between;
  margin-bottom: 2rem;
  font-weight: 600;
  text-transform: uppercase;
  margin-top: 5rem;
  padding-bottom: 2rem;
  /* border-bottom: #181a23 3px solid; */
}
.dashboard {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr)); 
  grid-gap: 1rem;
  text-transform: uppercase;
}
.dashboard .item {
  padding: 1rem;
  color: var(--text);
  background: var(--component);
  overflow: hidden;
  min-height: 7rem;
  transition: 0.2s;
  border-radius: 6px;
  border-left: #181a23 5px solid;
}
.dashboard .item:hover {
  background: #181a23;
  border-radius: 10px;
}

.dashboard .item::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  background: #181a23;
  transition: 0.2s;
}

.dashboard .item h2 {
  margin-top: 0;
  color: var(--subtext);
  font-size: 1rem;
  font-weight: 500;
}
data {
  letter-spacing: 0.1rem;
  font-size: 2rem;
}
#current-time {
  margin-top: -2rem;
  font-weight: 200;
  color: var(--subtext);
  font-size: 4rem;
  letter-spacing: 0.4rem;
}